/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
 .s_tizers .row > div {
  text-align: center;
}
 .tiz_descr {
	margin-top: 10px;
	display: block;
}
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
	.s_tizers .row > div {
 		 text-align:left;
}
 .tiz_descr {
		margin-top: 10px;
		display: inline-block;
}
section h2{
	text-align: center
}


}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
.top_phone, .logo, h1, .top_header h2{
 	text-align: center;

 }
 .logo{
 	margin-bottom: 15px;
 }
 .top_header,  {
 	text-align: center;
 	margin-top: 30px;

 }
 .tabs_header .tabs2{
	font-size: 14px;
 }
 .profy_item:last-child{
	margin-top:0px
}
.profy_item:nth-child(2){
	margin-top: 0px
}
.s_contacts .tabs {
    margin-left: 0;
    margin-top: 20px;

}
}
@media only screen and (max-width : 600px) {
	.profy_item{
		width: 100%;
		text-align: center;
		margin-bottom: 25px
	}



}
/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {

.top_phone .tab_item {
	font-size: 26px;
}
.tabs_header .tabs2 .tab2{
	font-size: 12px;
	padding: 15px 8px;

 }
 .button_wrap{
 	text-align: center;
 }
 section h2{
	font-size: 20px;
	line-height: 40px;
}
.s_contacts .tabs .tab {
	padding: 0 20px;
}
.s_contacts .tabs {
	margin-top: 15px;
	margin-left: 0
}
.s_contacts h2 {
	font-size: 40px;
}

}

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px) {
	h1{
		font-size: 55px
	}


}


